.cc-footer {
    background: #05050F;
    padding: 4rem 4% 2rem;
    color: #fff;
}
.cc-footer-items {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5rem;
}
.cc-footer-items-hd {
    color: #8C97AC;
    font-size: 14px;
    margin-bottom: 1.2rem;
}
.cc-footer-items-list {
    list-style: none;
    padding-left: 0;
    font-size: 13px;
    line-height: 30px;
    color: #E9F7FE;
}
.cc-footer-items-txt1 {
    font-size: 13px;
    margin-top: 2rem;
    width: 230px;
    color: #828282;
    line-height: 26px;
}
.cc-footer-items-txt2 {
    font-size: 13px;
    color: #E9F7FE;
    width: 220px;
    line-height: 25px;
}
.cc-footer-items-txt3 {
    font-size: 14px;
    color: #FFD166;
    line-height: 25px;
}
.cc-footer hr {
    border-color: #313D55;
}
.cc-ftr-cpy {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    align-items: center;
}
.cc-ftr-cpy p {
    margin-bottom: 0;
}
.cc-ftr-socials {
    display: flex;
    justify-content: space-between;
    width: 100px;
}
.cc-ftr-socials img {
    width: 14px;
    cursor: pointer;
}